.homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20 px 80 px;
}

.categories-container{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.categories-item{
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;

    &:first-child{
        margin-right: 7.5px;
    }

    &:last-child{
        margin-left: 7.5px;
    }


    .content-holder{
        height: 90px;
        padding: 0 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;

        .title{
            font-weight:bold ;
            margin-bottom: 6px;
            font-size: 22px;
            color: rgb(102, 250, 16);
        }

        .subtitle{
            font-weight: lighter;
            font-size: 16px;
        }
    }
}